/* harmony default export */ __webpack_exports__["default"] = ("<div class=\"container\">\n  \n  <h1 class=\"countheads mt-5 text-primary\">Conditions générales d'utilisation - Données personnelles</h1>\n  <h2>Design your privacy</h2>\n  \n  <br>\n  \n  <h3 class=\"nocount\"><u>Définitions</u> :</h3>\n  <p>\n    <b>L'Éditeur</b> : La personne, physique ou morale, qui édite les services de communication au public en ligne.<br>\n    <b>Le Site</b> : L'ensemble des sites, pages Internet, applications mobiles et services en ligne proposés par l'Éditeur.<br>\n    <b>L'Utilisateur</b> : La personne utilisant le Site et les services.<br>\n  </p>\n  \n  <h3>1. Nature des données collectées</h3>\n  <p><b>Dans le cadre de l'utilisation des Sites, l'Éditeur est susceptible de collecter les catégories de données suivantes concernant ses Utilisateurs :</b></p>\n  <div><p>\n    Données d'état-civil, d'identité, d'identification...\n  </p><p>\n    Données relatives à la vie personnelle (habitudes de vie, situation familiale, hors données sensibles ou dangereuses)\n  </p><p>\n    Informations d'ordre économique et financier (revenus, situation financière, situation fiscale...)\n  </p><p>\n    Données de connexion (adresses IP, journaux d'événements...)\n  </p></div>\n  \n  <h3>2. Agrégation des données</h3>\n  <p><b>Agrégation avec des données non personnelles</b>\n  <br>\n  Nous pouvons publier, divulguer et utiliser les informations agrégées (informations relatives à tous nos Utilisateurs ou à des groupes ou catégories spécifiques d'Utilisateurs que nous combinons de manière à ce qu'un Utilisateur individuel ne puisse plus être identifié ou mentionné) et les informations non personnelles à des fins d'analyse du secteur et du marché, de profilage démographique, à des fins promotionnelles et publicitaires et à d'autres fins commerciales.</p>\n  \n  <p><b>Agrégation avec des données personnelles disponibles sur les comptes sociaux de l'Utilisateur</b>\n    <br>\n    Si vous connectez votre compte à un compte d’un autre service afin de faire des envois croisés, ledit service pourra nous communiquer vos informations de profil, de connexion, ainsi que toute autre information dont vous avez autorisé la divulgation. Nous pouvons agréger les informations relatives à tous nos autres Utilisateurs, groupes, comptes, aux données personnelles disponibles sur l’Utilisateur.</p>\n      \n  <h3>3. Cookies</h3>\n  <p><b>Durée de conservation des cookies</b>\n    <br>\n    Conformément aux recommandations de la CNIL, la durée maximale de conservation des cookies est de 13 mois au maximum après leur premier dépôt dans le terminal de l'Utilisateur, tout comme la durée de la validité du consentement de l’Utilisateur à l’utilisation de ces cookies. La durée de vie des cookies n’est pas prolongée à chaque visite. Le consentement de l’Utilisateur devra donc être renouvelé à l'issue de ce délai.</p>\n    \n    <p><b>Finalité cookies</b><br>\n      Les cookies peuvent être utilisés pour des fins statistiques notamment pour optimiser les services rendus à l'Utilisateur, à partir du traitement des informations concernant la fréquence d'accès, la personnalisation des pages ainsi que les opérations réalisées et les informations consultées.<br>Vous êtes informé que l'Éditeur est susceptible de déposer des cookies sur votre terminal. Le cookie enregistre des informations relatives à la navigation sur le service (les pages que vous avez consultées, la date et l'heure de la consultation...) que nous pourrons lire lors de vos visites ultérieures.</p>\n   \n  <h3>4. Conservation des données techniques</h3>\n  <p>\n    <b>\n      Durée de conservation des données techniques\n    </b>\n    <br>\n    Les données techniques sont conservées pour la durée strictement nécessaire à la réalisation des finalités visées ci-avant.\n  </p>\n  \n  <h3>5. Délai de conservation des données personnelles et d'anonymisation</h3>\n  <p>\n    <b>\n      Suppression des données après suppression du compte\n    </b>\n    <br>\n    Des moyens de purge de données sont mis en place afin d'en prévoir la suppression effective dès lors que la durée de conservation ou d'archivage nécessaire à l'accomplissement des finalités déterminées ou imposées est atteinte. Conformément à la loi n°78-17 du 6 janvier 1978 relative à l'informatique, aux fichiers et aux libertés, vous disposez par ailleurs d'un droit de suppression sur vos données que vous pouvez exercer à tout moment en prenant contact avec l'Éditeur.\n  </p>\n  <p>\n    <b>\n      Suppression des données après 3 ans d'inactivité \n    </b>\n    <br>\n    Pour des raisons de sécurité, si vous ne vous êtes pas authentifié sur le Site pendant une période de trois ans, vous recevrez un e-mail vous invitant à vous connecter dans les plus brefs délais, sans quoi vos données seront supprimées de nos bases de données.\n  </p>\n  \n  \n  <h3>6. Suppression du compte</h3>\n  <p>\n    <b>\n      Suppression du compte à la demande\n    </b>\n    <br>\n    L'Utilisateur a la possibilité de supprimer son Compte à tout moment, par simple demande à l'Éditeur OU par le menu de suppression de Compte présent dans les paramètres du Compte le cas échéant.\n  </p>\n  <p>\n    <b>\n      Suppression du compte en cas de violation des CGU\n    </b>\n    <br>\n    En cas de violation d'une ou de plusieurs dispositions des CGU ou de tout autre document incorporé aux présentes par référence, l'Éditeur se réserve le droit de mettre fin ou restreindre sans aucun avertissement préalable et à sa seule discrétion, votre usage et accès aux services, à votre compte et à tous les Sites.\n  </p>\n  \n  <h3>7. Indications en cas de faille de sécurité décelée par l'Éditeur</h3>\n  <p>\n    <b>\n      Information de l'Utilisateur en cas de faille de sécurité\n    </b>\n    <br>\n    Nous nous engageons à mettre en oeuvre toutes les mesures techniques et organisationnelles appropriées afin de garantir un niveau de sécurité adapté au regard des risques d'accès accidentels, non autorisés ou illégaux, de divulgation, d'altération, de perte ou encore de destruction des données personnelles vous concernant. \n    Dans l'éventualité où nous prendrions connaissance d'un accès illégal aux données personnelles vous concernant stockées sur nos serveurs ou ceux de nos prestataires, ou d'un accès non autorisé ayant pour conséquence la réalisation des risques identifiés ci-dessus, nous nous engageons à : \n  </p><ul><li>Vous notifier l'incident dans les plus brefs délais ;</li>\n    <li>Examiner les causes de l'incident et vous en informer ; </li>\n    <li>Prendre les mesures nécessaires dans la limite du raisonnable afin d'amoindrir les effets négatifs et préjudices pouvant résulter dudit incident</li>\n  </ul>\n  <p></p>\n    \n  <p>\n    <b>\n      Limitation de la responsabilité\n    </b>\n    <br>\n    En aucun cas les engagements définis au point ci-dessus relatifs à la notification en cas de faille de sécurité ne peuvent être assimilés à une quelconque reconnaissance de faute ou de responsabilité quant à la survenance de l'incident en question.\n  </p>\n    \n    \n    \n    \n    \n    \n  <h3>8. Modification des CGU et de la politique de confidentialité</h3>\t\n  <p>\n    <b>\n      En cas de modification des présentes CGU, engagement de ne pas baisser le niveau de confidentialité de manière substantielle sans l'information préalable des personnes concernées\n    </b>\n    <br>\n    Nous nous engageons à vous informer en cas de modification substantielle des présentes CGU, et à ne pas baisser le niveau de confidentialité de vos données de manière substantielle sans vous en informer et obtenir votre consentement.\n  </p>\n  \n</div>");

