/* harmony default export */ __webpack_exports__["default"] = ("<div class=\"container\">\n\n  <div *ngIf=\"accountId == 'ASSVIE'\">\n    <div class=\"row\">\n      <div class=\"col-12 my-3\">\n        <h3 class=\"text-primary\">\n          Souscrire un contrat d'assurance vie\n        </h3>\n      </div>\n      <div class=\"col-12 pt-3 pb-5\">\n        <h4 class=\"text-secondary mt-4\">Où souscrire</h4>\n        <div>\n          Souscrire un contrat d'assurance vie se fait généralement auprès d'une banque ou d'un courtier en assurance, même si au final, le contrat est logé chez un assureur (CNP, AXA, ...).\n          <br><br>\n          Il est donc possible de souscrire directement à La Banque Postale, la Caisse d'Epargne... ou chez des banques ou courtiers en ligne.\n          <br><br>\n          Il vous faut de façon général une liste de justificatifs (identité, domicile, domiciliation bancaire), et un montant minimum d'ouverture du contrat.\n        </div>\n        \n        <h4 class=\"text-secondary mt-4\">Frais et rendement</h4>\n        <div>\n          Deux caractéristiques essentielles doivent être vérifiée avant l'ouverture d'un contrat : \n          <ul>\n            <li>\n              le rendement des fonds proposés\n            </li>\n            <li>\n              les frais appliqués sur votre encours\n            </li>\n          </ul>\n          Des frais trop importants peuvent tuer la performance de votre investissement à long terme !\n          <br><br>\n          Les banques et courtiers en lignes proposent les frais les plus bas du marché (inférieur à 1%).\n          Mieux encore, il n'y a pas (ou rarement) de frais sur les versements ou les arbitrages.\n          <br><br>\n          Attention, certaines offres en lignes, comme Yomoni Kids, sont dédiée aux enfants et mettent en avant des rendements attractifs, mais elles proposent des frais de 1.6% et un risque de perte en capital !\n        </div>\n        \n        <h4 class=\"text-secondary mt-4\">Alors quelle banque/courtier ?</h4>\n        <div>\n          Aujourd'hui, l'offre EasyVie de EasyBourse (filiale de la banque postale), propose une offre d'insvestissement complète (fond en euros, action, immobilier, ...) avec des taux de frais parmis les plus bas du marché, et un des meilleur rendement sur le fond euros (2.5% en 2018).\n          Seul reproche, l'application mobile n'est pas encore optimisée, esperons qu'elle soir mise à jour dans l'avenir.\n          <div class=\"col-12 col-sm-6 offset-md-3 p-5\">\n            <a class=\"btn btn-primary w-100\" href=\"https://www.easybourse.com/easyvie/offre/\" target=\"blank\">Découvrir le contrat EasyVie</a>\n          </div>\n          Vous pouvez aussi vous orienter vers Boursorama ou Binck, les frais sont identiques (0.75% par an), l'offre de supports d'investissement ou les performances des différents fonds les rendent toutefois moins attractifs.\n        </div>\n        \n      </div>\n    </div>\n  </div>\n\n</div>");

