let DiscoverComponent = class DiscoverComponent {
    constructor(route) {
        this.route = route;
    }
    ngOnInit() {
        const accountId = this.route.snapshot.paramMap.get('id');
        this.account = accounts.find(act => act.type === accountId);
    }
    getPresentation() {
        return {
            "TIR": `Avant d'avoir un portefeuille, nous avons tous eu une ou plusieurs tirelires.
Cette objet nous permet de conserver précieusement l'argent reçu en cadeau, et il est utilisé quelques fois pour des petites dépenses.

Inutile de préciser que la tirelire ne rapporte pas d'argent. Son rendement est même négatif si on considère l'inflation.`,
            "LIVA": `Le livret A est un compte épargne exonéré d'impôts. Son plafond est fixé à 22 950€ et son rendement est de 0.75%.

C'est le produit d'épargne préféré des français, et il n'est pas rare que les jeunes parents ouvrent un livret A pour y placer toutes les économies de leurs enfants.`,
            "BRUNO": `Consulter régulièrement ses comptes, planifier ses dépenses et épargner une partie de son salaire, pour soit et pour ses enfants, soyons clair : c'est compliqué.

Et si vous laissiez un gentil petit ours faire cela à votre place ?

C'est possible avec Bruno. Concrètement, vous lui donnez accès à votre compte en banque afin qu'il analyse vos dépenses et vous propose régulièrement d'épargner des sommes d'argent sur un compte rémunéré à 1% brut pour les premiers 10 000€.

Pour cela, vous interagissez directement avec Bruno via l'application Facebook Messenger, c'est simple et fluide d'utilisation.`,
            "KOALA": `Imaginez, vous acheter un frigo en ligne d'une valeur de 500€, et automatiquement, cela épargne 25€ pour vos enfants. Pas mal non ?

C'est le principe du Cashback, des entreprises font office d'intermédiaire de vente, et vous reverse un pourcentage de votre achat sur une cagnotte.

Il en existe plusieurs (IGraal, eBuyClub, ...) mais Capital Koala a retenu notre attention. Tout le fonctionnement de leur système de cashback est basé sur l'épargne des enfants.

Vous achetez en ligne et invitez aussi vos proches à le faire (grands parents, oncles et tantes ...), et cela génère de l'épargne pour votre enfant, sans sortir d'argent de votre poche !`,
            "LIVJEU": `Le livret Jeune est un produit d'épargne qui peut être ouvert entre 12 et 25 ans.

Son plafond est de 1600€ et il est rémunéré à un taux de 2%, net d'impôts.`,
            "ASSVIE": `L'assurance Vie est connue pour optimiser la transmission d'un patrimoine en cas de décès, mais ce n'est qu'une facette de ce produit.

C'est avant tout un placement financier à la fiscalité avantageuse qui permet de diversifier son épargne sur différents supports sécurisés ou risqués.
Certains contrats permettent même d'accéder à des investissements immobiliers.

L'épargne est à tout moment accessible, même un mois après l'ouverture du contrat. La fiscalité sur les plus-values s'allège en fonction de l'age du contrat.
Détenir un contrat pendant au moins 8 ans permet alors de bénéfichier d'une fiscalité optimale sur le contrat.

Il est possible d'ouvrir autant de contrat d'assurance vie qu'on le souhaite, dans n'importe quel établissement.

Le fonctionnement est assez souple : vous pouvez verser des sommes d'argent régulièrement ou ponctuellement.`,
            "YOMONIKIDS": `Yomoni Kids est une assurance vie 100% en ligne. 
Contrairement à un contrat d'assurance vie classique, vous choisissez simplement un "Niveau de risque" sur votre contrat. Yomoni placera l'argent automatiquement sur des supports respectant ce niveau de risque.
En fonction de votre profil de risque, les frais de gestion varient entre 0.8% et 1.6%.`,
            "PEE": `Le PEE est un plan d'épargne proposé par les entreprises disposant d'un accord de participation. 

Il permet d'investir sur différents supports et l'employeur abonde parfois les versements sous certaines conditions.

Les versements sont bloqués 5 ans, sauf conditions exceptionnelles de déblocage (achat résidence principale, naissance ou adoption du troisième enfant...).`,
            "PELCEL": ``,
        }[this.account.type];
    }
    getInterest() {
        return {
            "TIR": `Cet objet va devenir indispensable pour plusieurs raisons.
Votre enfant va recevoir de l'argent liquide à un moment ou à un autre : noël, anniversaire. Il faudra bien un endroit où le ranger.

Puis, en grandissant, il pourra - sous votre contrôle - utiliser cet argent pour se payer un tour de manège, des bonbons, ou vous offrir des cadeaux.

Il n'est pas nécessaire de conserver des centaines d'euros dans une tirelire. 
Tout l'argent qui n'est pas utile à court terme trouvera mieux sa place sur un placement au rendement plus élevé.

Petite astuce : vous avez besoin de retirer de l'argent au distributeur ? Prenez l'argent dans la tirelire de votre enfant et faites un virement de la même somme sur une épargne ou un placement optimisé, tout le monde sera gagnant.`,
            "LIVA": `La grande force du livret A est sa simplicité d'utilisation et la disponibilité des fonds. Son rendement est plutôt une faiblesse si vous souhaitez booster l'épargne de votre enfant.

En effet, avec un rendement inférieur à l'inflation, techniquement, on perd de l'argent en épargnant sur ce produit... mais on en gagne toujours plus que l'épargne d'une tirelire !

Il est donc important d'utiliser le livret A (et tous les produits d'épargne) comme un vase communiquant :
Transférer l'argent du livret A vers un placement optimisé lorsque vous avez dépassé un montant maximum.

Votre livret A doit vous permettre d'accumuler des fonds avant de les placer.`,
            "BRUNO": `Bruno, c'est l'outil indispensable pour les personnes qui ne savent pas par où commencer pour épargner. 
L'ours s'occupe de tout à votre place.

Vous ne pouvez pas ouvrir de compte pour vos enfants, mais rien de vous empêche de l'ouvrir à votre nom. 
Lorsque vous avez accumulez suffisament d'épargne, vous pouvez transférer l'argent sur des placements optimisés pour chacun de vos enfants.
`,
            "KOALA": `Ce produit d'épargne ne va pas vous permettre d'économiser des milliers d'euros par an pour vos enfants, soyons clair.
Par contre, il peut sans efforts générer plusieurs dizaines voire centaines d'euros si vous achetez souvent vos produits en lignes.

Ce sont des sommes rondelettes dont il serait dommage de ne pas profiter, surtout que cela ne demande pas spécialement d'efforts à l'usage, et ça ne coute rien !

Le compte n'est cependant pas rémunéré, il est donc intéressant de transférer les fonds vers un livret A ou un placement optimisé une fois un certain montant atteint.`,
            "LIVJEU": `Ce produit d'épargne possède un bon rendement et une fiscalité avantageuse, cependant, son plafond de 1 600€ ne permet pas de le considérer comme un placement.

L'âge minimum d'ouverture du livret (12 ans) peut correspondre à une période où l'on sensibilise l'enfant à la gestion de son argent, autant l'utiliser dans ce sens.

Vous pouvez verser de l'argent de poche sur ce livret, lui apprendre la notion d'intérêts composés et lui permettre d'épargner pour des projets à moyen terme qui lui tiennent à cœur (console, téléphone, instruments de musique...).`,
            "ASSVIE": `Les rendements des contrats d'assurance vie, même sur des supports 100% sécurisés, sont supérieurs aux produits d'épargne (au moins 2% en 2018 pour les fonds euros).

C'est un excellent placement pour les investissements long terme, surtout si on souhaite bénéficier d'une fiscalité avantageuse. 

Cela tombe bien pour nos enfants, nous avons du temps !

Si votre enfant à 10 ans par exemple, ouvrir un contrat d'assurance vie à cette période vous permettra de bénéficier d'une fiscalité optimale à sa majorité.

Cet horizon de placement et ces rendements en font par conséquent le produit idéal pour préparer l'avenir de vos enfants.
`,
            "YOMONIKIDS": `Ce contrat d'assurance vie est peut être ouvert avec un capital de 300 €, c'est un contrat accessible. Toutefois, il est conseillé aux personnes souhaitant placer l'argent de leurs enfants avec un risque de perte en capital.

Par exemple, 1000 € d'investissement peuvent devenir 800, 500 voire même 100 € en pleine crise financière. Il faut donc utiliser les différents niveaux de risque en parfaite connaissance de cause et être capable d'arbitrer son contrat en fonction de l'actualité économique.

Si vous souhaitez l'ouvrir pour finalement rester à un risque 0, alors comparer les autres offres du marché de l'assurance vie, il n'y en a des plus performants, avec des frais deux fois moins élevés`,
            "PEE": `Ce placement n'est à la base pas dédié aux enfants, mais il est possible de détourner son usage pour en faire une solution de placement efficace pour ses enfants.

Si votre employeur propose un abondement sur les versements, vous pouvez répartir les sommes versés par l'employeur pour vos enfants.

Par exemple, je verse 100 € sur mon PEE, mon employeur abonde cette somme de 40€, ces 40€ seront répartis entre mes enfants, et le suivi de ces sommes est fait dans l'application.`,
            "PELCEL": `En cours de rédaction.`,
        }[this.account.type];
    }
    getSubscribe() {
        return {
            "TIR": `Il n'y a pas vraiment de souscription pour une tirelire, trouvez simplement la tirelire qui vous fait envie (cochon, boîte à musique, ...).
Il en existe dans les magasins de décoration, et des centaines sur internet.
Si vous aimez le Do It Yourself, pourquoi ne pas acheter une tirelire à personnaliser vous-même ?`,
            "LIVA": `Le Livret A peut être ouvert dans n'importe quelle banque. Pour plus de simplicité, il peut être ouvert dans la même banque qu'un des deux parents. 

L'ouvrir dans une autre banque ne procure pas d'avantages particuliers et dans tous les cas, le livret A peut-être clôturé facilement pour être ouvert dans une autre banque.

Veuillez toutefois à ne pas ouvrir deux livrets A pour une seule personne, c'est interdit.`,
            "BRUNO": `Pour souscrire à un compte Bruno, vous devez utiliser Facebook Messenger.

Vous trouverez tous les détails directement sur le site en cliquant sur le lien suivant :`,
            "KOALA": `Pour souscrire à Capital Koala, rendez-vous sur le site en cliquant sur le lien suivant.

N'hésitez pas à faire découvrir et à parrainer vos proches, vous récolterez une partie de leurs cashback en plus du votre :)`,
            "LIVJEU": `Tout comme le Livret A, il peut être ouvert dans n'importe quelle banque et il est facilement transférable d'une banque à l'autre.

Pour plus de simplicité, il peut être ouvert dans la même banque qu'un des deux parents.`,
            "ASSVIE": `Vous pouvez souscrire un ou plusieurs contrat d'assurance vie auprès de votre banque ou d'autres organismes spécialisés.
Un montant minimum peut être demandé à l'ouverture.

Il est important de sélectionner un contrat avec un bon rendement sur le fond en euros si vous souhaitez sécuriser complètement l'épargne. 
Il est encore plus important de trouver contrats sans frais sur les versements et avec les frais de gestion les plus faibles afin de ne pas "plomber" votre rendement.

Pour cela, les offres en lignes sont les meilleurs : boursorama, Linxea, ...

Une question se posera à l'ouverture : à quel nom ouvrir le contrat ? Celui de son enfant ou le votre ?

Dans les deux cas, il est possible de contrôler l'usage des fonds (notion de bénéficiare acceptant) afin qu'il ne dilapide pas l'argent une fois sa majorité atteinte.

Le plus simple semble toutefois de l'ouvrir à votre nom. 
La limite de cette solution est qu'il ne faut pas verser plus de 100 000 euros à son enfant tous les 15 ans, sous peine d'être imposé sur le don.

Envie d'aller plus loin ? Découvrez les contrats les plus performants en cliquant sur le lien suivant :`,
            "YOMONIKIDS": `Pour souscrire à Yomoni Kids, rendez-vous sur le site en cliquant sur le lien suivant :`,
            "PEE": `Cette partie est en cours de rédaction.`,
            "PELCEL": `Cette partie est en cours de rédaction.`,
        }[this.account.type];
    }
};
